.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.modal-container {
  background-color: white;
  border-radius: 0.75rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.modal-header {
  padding: 1.5rem 1.5rem 1rem 1.5rem;
  border-bottom: 1px solid #e5e7eb;
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.modal-header .modal-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #111827;
  margin: 0;
}
.modal-header svg {
  width: 1.25rem;
  height: 1.25rem;
  color: #3b82f6;
}

.modal-form {
  padding: 1.5rem;
}
.modal-form .form-section {
  margin-bottom: 2rem;
}
.modal-form .form-section:last-child {
  margin-bottom: 0;
}
.modal-form .form-section .section-header {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
  gap: 0.75rem;
}
.modal-form .form-section .section-header svg {
  width: 1.25rem;
  height: 1.25rem;
  color: #9ca3af;
}
.modal-form .form-section .section-header .section-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0;
}
.modal-form .form-section .form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}
.modal-form .form-section .form-row:last-child {
  margin-bottom: 0;
}
.modal-form .form-section .form-field {
  margin-bottom: 1rem;
}
.modal-form .form-section .form-field:last-child {
  margin-bottom: 0;
}
.modal-form .form-section .form-field .field-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.5rem;
}
.modal-form .form-section .form-field .field-label .required {
  color: #ef4444;
}
.modal-form .form-section .form-field .select-container {
  position: relative;
}
.modal-form .form-section .form-field .select-container .form-select {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: white;
  appearance: none;
  cursor: pointer;
  transition: border-color 0.2s;
}
.modal-form .form-section .form-field .select-container .form-select:focus {
  outline: none;
  border-color: #3b82f6;
}
.modal-form .form-section .form-field .select-container .form-select.error {
  border-color: #ef4444;
}
.modal-form .form-section .form-field .select-container .select-arrow {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  color: #6b7280;
  pointer-events: none;
}
.modal-form .form-section .form-field .form-input {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  transition: border-color 0.2s;
}
.modal-form .form-section .form-field .form-input:focus {
  outline: none;
  border-color: #3b82f6;
}
.modal-form .form-section .form-field .form-input.error {
  border-color: #ef4444;
}
.modal-form .form-section .form-field .form-textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  resize: vertical;
  min-height: 80px;
  transition: border-color 0.2s;
}
.modal-form .form-section .form-field .form-textarea:focus {
  outline: none;
  border-color: #3b82f6;
}
.modal-form .form-section .form-field .error-message {
  color: #ef4444;
  font-size: 0.75rem;
  margin-top: 0.25rem;
  min-height: 1rem;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem 1.5rem 1.5rem 1.5rem;
  border-top: 1px solid #e5e7eb;
  margin: 0 -1.5rem -1.5rem -1.5rem;
}
.modal-footer .btn {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
}
.modal-footer .btn.btn-secondary {
  background-color: white;
  color: #374151;
  border: 1px solid #d1d5db;
}
.modal-footer .btn.btn-secondary:hover {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.modal-footer .btn.btn-primary {
  background-color: #3b82f6;
  color: white;
}
.modal-footer .btn.btn-primary:hover {
  background-color: #2563eb;
}
.modal-footer .btn.btn-primary:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

@media (max-width: 640px) {
  .modal-container {
    margin: 0.5rem;
    max-height: calc(100vh - 1rem);
  }
  .modal-form {
    padding: 1rem;
  }
  .modal-form .form-section .form-row {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  .modal-footer {
    flex-direction: column;
    gap: 0.75rem;
    margin: 0 -1rem -1rem -1rem;
  }
  .modal-footer .btn {
    width: 100%;
  }
}
@media (max-width: 480px) {
  .modal-header {
    padding: 1rem 1rem 0.75rem 1rem;
  }
  .modal-header .modal-title {
    font-size: 1.125rem;
  }
  .modal-form {
    padding: 0.75rem;
  }
  .modal-footer {
    padding: 0.75rem 1rem 1rem 1rem;
    margin: 0 -0.75rem -0.75rem -0.75rem;
  }
}
